import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { OrganizationsResolver } from '@organizations/services/organizations-resolver.service';
import { ProjectResolver } from '@projects/services/project-resolver.service';
import { ProjectsResolver } from '@projects/services/projects-resolver.service';

import { ProjectMembersComponent } from './pages/project-details/components/project-members/project-members.component';
import { ProjectsUsersResolver } from './pages/project-details/components/project-members/services/projects-users-resolver.service';
import { ProjectSettingsComponent } from './pages/project-details/components/project-settings/project-settings.component';
import { ProjectDetailsComponent } from './pages/project-details/project-details.component';
import { ProjectsIndexComponent } from './pages/projects-index/projects-index.component';

const routes: Routes = [
  {
    path: '',
    component: ProjectsIndexComponent,
    resolve: {
      projects: ProjectsResolver,
      organizations: OrganizationsResolver,
    },
    data: {
      shadow: true,
    },
  },
  {
    path: ':project_id',
    component: ProjectDetailsComponent,
    resolve: {
      project: ProjectResolver,
    },
    children: [
      {
        path: '',
        component: ProjectMembersComponent,
        resolve: {
          members: ProjectsUsersResolver,
        },
      },
      {
        path: 'settings',
        component: ProjectSettingsComponent,
        resolve: {
          project: ProjectResolver,
        },
      },
      {
        path: 'members',
        component: ProjectMembersComponent,
        resolve: {
          members: ProjectsUsersResolver,
        },
      },
    ],
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [RouterModule],
})
export class ProjectsRoutingModule { }
