import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { map } from 'rxjs/operators';

import { fadeInRight400ms, fadeInUp400ms, scaleFadeIn400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';
import theme from '@vex/utils/tailwindcss';
import { trackById } from '@vex/utils/track-by';

import { OrganizationResource } from '@core/http/organization-resource.service';
import { ProjectResource } from '@core/http/project-resource.service';
import { Organization, Project } from '@data/schema';
import { OrganizationsCreateComponent } from '@organizations/components/organizations-create/organizations-create.component';
import { OrganizationDataService } from '@organizations/services/organization-data.service';
import { ProjectsCreateComponent } from '@projects/components/projects-create/projects-create.component';
import { ProjectDataService } from '@projects/services/project-data.service';
import { ProjectsDataService } from '@projects/services/projects-data.service';
import { UserDataService } from '@users/services/user-data.service';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { ProjectsIndexIcons } from './services/projects-index-icons.service';

@Component({
  selector: 'projects-index',
  templateUrl: './projects-index.component.html',
  styleUrls: ['./projects-index.component.scss'],
  animations: [
    scaleIn400ms,
    fadeInRight400ms,
    stagger40ms,
    fadeInUp400ms,
    scaleFadeIn400ms,
  ],
})
export class ProjectsIndexComponent implements OnInit, OnDestroy {

  projects: Project[];
  organizations: Organization[];
  selectedOrganization: Organization;

  trackById = trackById;
  theme = theme;

  icons;

  constructor(
    private dialog: MatDialog,
    private layoutConfigService: LayoutConfigService,
    private projectDataService: ProjectDataService,
    private projectsDataService: ProjectsDataService,
    private projectIndexIcons: ProjectsIndexIcons,
    private projectResource: ProjectResource,
    private organizationDataService: OrganizationDataService,
    private organizationResource: OrganizationResource,
    private route: ActivatedRoute,
    private router: Router,
    private userData: UserDataService
  ) {
    this.layoutConfigService.setIkaros();
  }

  ngOnInit() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.projects = snapshot.data.projects.map(project => {
      return new Project(project);
    });
    this.projectsDataService.set(this.projects);

    this.organizations =  this.route.snapshot.data.organizations.map(org => {
      return new Organization(org);
    });

    this.selectedOrganization = this.organizations.find(organization => {
      return organization.id === snapshot.queryParams.organization_id;
    });
    this.selectedOrganization = this.selectedOrganization || this.organizations[0];
    this.organizationDataService.set(this.selectedOrganization);

    // Set no project as selected
    this.projectDataService.set(null);

    // Expose icons to view
    this.icons = this.projectIndexIcons;
  }

  createProject(data) {
    data.organization_id = this.selectedOrganization.id;

    this.projectResource.create(data).subscribe(
      (res: Project) => {
        this.projects.unshift(res);
      },
      err => {

      });
  }

  updateProject(id: Project['id'], data) {
    this.projectResource.update(id, data).subscribe(
      (res: Project) => {
        this.getProjects();
      },
      err => {

      });
  }

  getProjects(organizationId?: Organization['id']) {
    const params: any = {};

    if (organizationId) {
      params.organization_id = organizationId;
    }

    this.projectResource.index(params).subscribe(
      (res: Project[]) => {
        this.projects = res;
      },
      error => {

      }
    );
  }

  deleteProject(projectId: Project['id']) {
    this.projectResource.destroy(projectId).subscribe(
      (res: Project[]) => {
        this.projects = this.projects.filter((project: Project) => {
          return project.id !== projectId;
        });
      },
      error => {

      }
    );
  }

  getOrganizations() {
    this.organizationResource.index().subscribe(
      (res: Organization[]) => {
          this.organizations = res;
      },
      err => {

      });
  }

  createOrganization(data) {
    this.organizationResource.create(data).subscribe(
      (res: Organization) => {
        this.organizations.push(res);
        this.organizations = this.organizations.slice(0);
      },
      err => {

      });
  }

  viewOrganization(organization: Organization) {
    this.getProjects(organization.id);
    this.selectedOrganization = organization;
    this.organizationDataService.set(this.selectedOrganization);

    // Add queryParam to URL
    this.router.navigate([], {
      queryParams: organization.isShadow ? {} : {organization_id: organization.id},
    });
  }

  openCreateDialog() {
    const dialogRef = this.dialog.open(ProjectsCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createProject($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  openUpdateDialog(project: Project) {
    const dialogRef = this.dialog.open(ProjectsCreateComponent, {
      width: '600px',
      data: project,
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(
      ($event) => {
        this.updateProject(project.id, $event);
      }
    );

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  showRequests(projectId: Project['id']) {
    const path: string = '/requests?project_id=' + projectId;

    this.router.navigateByUrl(path);
  }

  showProjectMembers(projectId: Project['id']) {
    const path = `/projects/${projectId}/members`;

    this.router.navigateByUrl(path);
  }

  showUserOrganizations() {
    const path = `/users/${this.userData.user.id}/organizations`;

    this.router.navigateByUrl(path);
  }

  showOrganization() {
    const path = `/organizations/${this.selectedOrganization.id}/settings`;

    this.router.navigateByUrl(path);
  }

  toggleStar(id: Project['id']) {
    const project = this.projects.find(c => c.id === id);

    if (project) {
      project.starred = !project.starred;
      this.updateProject(project.id, project);
    }
  }

  openCreateOrganizationDialog() {
    const dialogRef = this.dialog.open(OrganizationsCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createOrganization($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
