import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import icBusiness from '@iconify/icons-ic/twotone-business';
import icChat from '@iconify/icons-ic/twotone-chat';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icMail from '@iconify/icons-ic/twotone-mail';
import icPeople from '@iconify/icons-ic/twotone-people';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icStar from '@iconify/icons-ic/twotone-star';
import icStarBorder from '@iconify/icons-ic/twotone-star-border';

import icEdit from '@iconify/icons-ic/twotone-edit';
import icLink from '@iconify/icons-ic/twotone-link';

import { Project } from '@data/schema/project';

@Component({
  selector: 'projects-card',
  templateUrl: './projects-card.component.html',
  styleUrls: ['./projects-card.component.scss'],
})
export class ProjectsCardComponent implements OnInit {

  @Input() project: Project;
  @Output() members = new EventEmitter<Project['id']>();
  @Output() toggleStar = new EventEmitter<Project['id']>();
  @Output() delete = new EventEmitter<Project['id']>();
  @Output() update = new EventEmitter<Project>();
  @Output() view = new EventEmitter<Project['id']>();

  icBusiness = icBusiness;
  icPhone = icPhone;
  icMail = icMail;
  icChat = icChat;
  icStar = icStar;
  icStarBorder = icStarBorder;
  icPeople = icPeople;

  icEdit = icEdit;
  icLink = icLink;
  icDelete = icDelete;

  constructor() { }

  ngOnInit() {
  }

  emitToggleStar(event: MouseEvent, projectId: Project['id']) {
    event.stopPropagation();
    this.toggleStar.emit(projectId);
  }
}
