import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import icClose from '@iconify/icons-ic/twotone-close';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icEmail from '@iconify/icons-ic/twotone-mail';
import icPerson from '@iconify/icons-ic/twotone-person';

@Component({
  selector: 'projects-update',
  templateUrl: './projects-update.component.html',
  styleUrls: ['./projects-update.component.scss'],
})
export class ProjectsUpdateComponent implements OnInit {
  onUpdate = new EventEmitter<object>();

  form = this.fb.group({
    name: null,
    description: null,
  });

  icClose = icClose;
  icDelete = icDelete;
  icPerson = icPerson;
  icEmail = icEmail;

  constructor(
    @Inject(MAT_DIALOG_DATA) public project: any,
    private dialogRef: MatDialogRef<ProjectsUpdateComponent>,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.form.patchValue(this.project || {});
  }

  save() {
    this.onUpdate.emit(this.form.value);
    this.dialogRef.close();
  }
}
