import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';

import { ProjectResource } from '@core/http/project-resource.service';
import { Project } from '@data/schema';
import { ProjectsUpdateComponent } from '@projects/components/projects-update/projects-update.component';
import { ProjectDataService } from '@projects/services/project-data.service';

import { ProjectSettingsIcons } from './services/project-settings-icons.service';

@Component({
  selector: 'project-settings',
  templateUrl: './project-settings.component.html',
  styleUrls: ['./project-settings.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class ProjectSettingsComponent implements OnInit {
  project: Project;

  links: Link[] = [
    {
      label: 'MEMBERS',
      route: './',
    },
    {
      label: 'BILLING',
      route: '',
      disabled: false,
    },
  ];

  icons;

  constructor(
    private dialog: MatDialog,
    private projectResource: ProjectResource,
    private projectDataService: ProjectDataService,
    private projectSettingsIcons: ProjectSettingsIcons,
    private route: ActivatedRoute,
    private router: Router
  ) {  }

  ngOnInit() {
    this.projectDataService.project$.subscribe(project => {
      this.project = new Project(project);
    });

    this.icons = this.projectSettingsIcons;
  }

  scenariosPath() {
    return '/scenarios';
  }

  projectQueryParams(project) {
    return {
      project_id: project.id,
    };
  }

  openUpdateProjectDialog() {
    const dialogRef = this.dialog.open(ProjectsUpdateComponent, {
      width: '600px',
      data: this.project,
    });

    const onUpdateSub = dialogRef.componentInstance.onUpdate.subscribe(($event) => {
      this.updateProject($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onUpdateSub.unsubscribe();
    });
  }

  updateProject(data) {
    this.projectResource.update(this.project.id, data).subscribe(
      (res: Project) => {
        this.project = res;
        this.projectDataService.set(res);
      },
      err => {

      });
  }
}
