import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { ProjectsUserResource } from '@core/http/projects-user-resource.service';

@Injectable({
  providedIn: 'root',
})
export class ProjectsUsersResolver implements Resolve<any> {

  constructor(
    private projectsUserResource: ProjectsUserResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.projectsUserResource.index(route.parent.params.project_id);
  }
}
