import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import icAccessTime from '@iconify/icons-ic/twotone-access-time';
import icAdd from '@iconify/icons-ic/twotone-add';
import icCheck from '@iconify/icons-ic/twotone-check';
import icMail from '@iconify/icons-ic/twotone-mail';
import icPersonAdd from '@iconify/icons-ic/twotone-person-add';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icWhatshot from '@iconify/icons-ic/twotone-whatshot';
import icWork from '@iconify/icons-ic/twotone-work';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';
import { AvatarComponent } from 'ngx-avatar';

import { Project } from '@data/schema/project';
import { ProjectDataService } from '@projects/services/project-data.service';

@Component({
  selector: 'project-details',
  templateUrl: './project-details.component.html',
  styleUrls: ['./project-details.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class ProjectDetailsComponent implements OnInit {

  project: Project;

  links: Link[] = [
    {
      label: 'SETTINGS',
      route: 'settings',
      active: () => this.isActive('settings'),
    },
    {
      label: 'MEMBERS',
      route: 'members',
      active: () => this.isActive('members'),
    },
  ];

  icWork = icWork;
  icPhone = icPhone;
  icPersonAdd = icPersonAdd;
  icCheck = icCheck;
  icMail = icMail;
  icAccessTime = icAccessTime;
  icAdd = icAdd;
  icWhatshot = icWhatshot;

  constructor(
    private route: ActivatedRoute,
    private projectDataService: ProjectDataService
  ) { }

  ngOnInit() {
    this.project =  this.route.snapshot.data.project;
    this.projectDataService.set(this.project);
  }

  isActive(path: string) {
    return this.route.firstChild.routeConfig.path === path;
  }
}
