import { SelectionModel } from '@angular/cdk/collections';
import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelectChange } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { untilDestroyed } from 'ngx-take-until-destroy';
import { filter } from 'rxjs/operators';

import { fadeInUp400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { ProjectsUserResource } from '@core/http/projects-user-resource.service';
import { FileService } from '@core/utils/file.service';
import { ProjectsUser } from '@data/schema';
import { ProjectMembersCreateComponent } from './components/project-members-create/project-members-create.component';
import { aioTableData, aioTableLabels } from './mock/project-members-data';
import { ProjectMembersDataService } from './services/project-members-data.service';
import { ProjectMembersIcons } from './services/project-members-icons.service';
import { ProjectsUserRoleData } from './services/projects-user-role-data.service';

@Component({
  selector: 'project-members',
  templateUrl: './project-members.component.html',
  styleUrls: ['./project-members.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class ProjectMembersComponent implements OnInit, AfterViewInit, OnDestroy {

  layoutCtrl = new FormControl('boxed');

  // Table settings
  @Input()
  columns: TableColumn<ProjectsUser>[] = [
    { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
    { label: 'Role', property: 'role', type: 'dropdown', visible: true},
    { label: 'Name', property: 'name', type: 'text', visible: true},
    { label: 'Email', property: 'email', type: 'text', visible: true},
  ];
  pageIndex = this.route.snapshot.queryParams.page || 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 20, 50];
  dataSource: MatTableDataSource<ProjectsUser> | null;
  selection = new SelectionModel<ProjectsUser>(true, []);
  searchCtrl = new FormControl();
  labels = aioTableLabels;

  // Breadcrumb settings
  crumbs = [{
    name: 'ProjectsUsers',
  }];

  icons;
  roleData;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private file: FileService,
    private location: Location,
    private projectMemberIcons: ProjectMembersIcons,
    private projectMembersDataService: ProjectMembersDataService,
    private projectsUserResource: ProjectsUserResource,
    private projectsUserRoleData: ProjectsUserRoleData,
    private router: Router,
    private route: ActivatedRoute
  ) { }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  ngOnInit() {
    this.dataSource = new MatTableDataSource();
    this.projectMembersDataService.members$.subscribe((members: ProjectsUser[]) => {
      this.dataSource.data = members;
    });

    const projectMembers = this.route.snapshot.data.members.map(member => {
      return new ProjectsUser(member);
    });
    this.projectMembersDataService.set(projectMembers);

    this.searchCtrl.valueChanges.pipe(
      untilDestroyed(this)
    ).subscribe(value => this.onFilterChange(value));

    this.icons = this.projectMemberIcons;
    this.roleData = this.projectsUserRoleData;
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getProjectsUsers(params) {
    const projectId = this.getProjectId();

    this.projectsUserResource.index(
      projectId, params
    ).subscribe(
      (res: ProjectsUser[]) => {
        this.projectMembersDataService.set(res);
      },
      error => {

      }
    );
  }

  createProjectsUser(data: FormData) {
    const projectId = this.getProjectId();

    this.projectsUserResource.create(
      projectId, data
    ).subscribe(
      res => {
        const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

        this.getProjectsUsers(projectId);
      },
      error => {

      }
    );
  }

  showProjectsUser(member: ProjectsUser) {
    const path: string = this.file.join('/', 'users', member.id, 'profile');
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path]);
  }

  destroyProjectsUser(member: ProjectsUser) {
    const projectId = this.getProjectId();

    this.projectsUserResource.destroy(
      projectId, member.id
    ).subscribe(res => {
      this.projectMembersDataService.delete(member.id);
      this.selection.deselect(member);
    });
  }

  destroyProjectsUsers(projectMembers: ProjectsUser[]) {
    projectMembers.forEach(r => this.destroyProjectsUser(r));
  }

  updateProjectsUser(member: ProjectsUser, params: object) {
    const projectId = this.getProjectId();

    this.projectsUserResource.update(
      projectId, member.id, params
    ).subscribe(res => {
      for (const key in params) {
        if (params.hasOwnProperty(key)) {
          member[key] = params[key];
        }
      }
    });
  }

  /**
   *
   * Table methods
   *
   */
  openCreateDialog() {
    const dialogRef = this.dialog.open(ProjectMembersCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(
      ($event) => {
        this.createProjectsUser($event);
      }
    );

    dialogRef.afterClosed().subscribe((member: ProjectsUser) => {
      onCreateSub.unsubscribe();
    });
  }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  /**
   * On page change, add queryParam 'page' to URL
   */
  onPaginateChange($event) {
    const queryParams = Object.assign({}, this.route.snapshot.queryParams);
    queryParams.page = $event.pageIndex;

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  getProjectId() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const projectId = snapshot.parent.params.projectId;
    return projectId;
  }

  ngOnDestroy() { }
}
