import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { ProjectsUser } from '@data/schema/projects-user';

@Injectable({
  providedIn: 'root',
})
export class ProjectMembersDataService {
  private subject = new BehaviorSubject(null);
  members$ = this.subject.asObservable();
  members: ProjectsUser[];

  constructor() { }

  set(members: ProjectsUser[]) {
    this.members = members;
    this.subject.next(members);
  }

  add(member: ProjectsUser) {
    this.members.unshift(new ProjectsUser(member));
    this.set(this.members);
  }

  delete(id: number) {
    this.members.splice(
      this.members.findIndex((existingProjectsUser) => {
        return existingProjectsUser.id === id;
      }), 1);

    this.set(this.members);
  }
}
